body, html {
  height: 100%;
}

h2, h3, h4, h5, h6,
.h2, .h3, .h4, .h5, .h6,
ol, p, ul {
  margin-bottom: 22px;
  margin-top: 0;
}

body {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.8;
}

label {
  line-height: 1;
}

h1, .h1 {
  margin-bottom: 44px;
}

strong, .strong {
  font-weight: 400;
}

/** Bootstrap overrides **/
.btn {
  padding-bottom: 12px;
  padding-top: 12px;
}

/** **/
.bottom {
  margin-bottom: 0;
  padding-bottom: 0;
}

.col-sm-offset-1-half {
  margin-left: 12.49999%;
}

@media (max-width: 767px) {
  .col-sm-offset-1-half {
    margin-left: 0;
  }
}

.first {
  margin-left: 0;
  padding-left: 0;
}

.last {
  margin-right: 0;
  padding-right: 0;
}

.top {
  margin-top: 0;
  padding-top: 0;
}

/** Sections. */
footer {
  background-color: #333333;
  color: #a9a9a9;
  font-family: Verdana, sans-serif;
  font-size: 11px;
  padding-bottom: 20px;
  padding-top: 20px;
}

@media (max-width: 767px) {
  footer .text-right {
    text-align: left;
  }
  .intel-report .caption {
    text-align: center !important;
  }
}

footer a {
  color: #fff;
}

footer a:hover {
  color: #fff;
}

footer ul {
  margin: 0;
  padding: 0;
}

footer ul.menu {
  margin-left: -2px;
  margin-top: 5px;
}

footer ul.menu li {
  padding-left: 2px;
  padding-right: 2px;
}

footer ul.social {
  margin-left: -15px;
}

footer ul.social li {
  padding-left: 15px;
  padding-right: 15px;
}

header {
  left: 0;
  padding-top: 40px;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

@media (max-width: 767px) {
  header img {
    width: 100px;
  }
}

header ul.list-inline {
  text-align: center;
}

header ul.list-inline li {
  padding-left: 15px;
  padding-right: 15px;
}

section {
  padding-bottom: 120px;
  padding-top: 120px;
}

#call-to-action {
  bottom: 80px;
  position: fixed;
  right: 0;
}

.about {
  background-color: #eef3f4;
}

.agents {
  padding-bottom: 0px;
}

.agents h2 {
  font-weight: 700;
}

.banners {
  height: 100%;
  padding: 0;
  position: relative;
}

.banners .item {
  background-size: cover;
  height: 100%;
  position: relative;
}

.banners .item .blurb {
  color: #000;
  font-size: 22px;
  position: absolute;
  top: 250px;
  width: 100%;
}

@media (max-width: 767px) {
  .banners .item .blurb {
    top: 100px;
  }
  .banners .item .blurb .lead {
    line-height: 1;
  }
}

.banners .item .blurb hr {
  border-top: 4px solid #000;
  margin: 11px auto 11px;
  width: 36px;
}

.banners .item .blurb p {
  margin-bottom: 10px;
}

.banners .item .blurb .lead {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 52px;
}

.banners .item .blurb .small {
  display: inline-block;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 0;
}

.banners .scroll-down {
  bottom: 30px;
  left: 50%;
  margin-left: -23px;
  position: absolute;
}

.case-studies {
  background-color: #d4e4ed;
}

.case-studies h2 {
  font-weight: 700;
}

.case-studies .subtitle {
  color: #5a5a5a;
  font-weight: 700;
  font-size: 24px;
}

.case-studies .wholesale.item {
  background-color: #FFF;
  -webkit-box-shadow: 10px 10px 0px 0px #bed6e3;
  box-shadow: 10px 10px 0px 0px #bed6e3;
  margin-bottom: 50px;
  margin-right: 0px;
  margin-left: 0px;
  padding: 40px 30px;
}

.case-studies .financial.item {
  background-color: #FFF;
  -webkit-box-shadow: 10px 10px 0px 0px #d1cddf;
  box-shadow: 10px 10px 0px 0px #d1cddf;
  margin-bottom: 50px;
  margin-right: 0px;
  margin-left: 0px;
  padding: 40px 30px;
}

.case-studies .item hr {
  border-top: 4px solid #00649f;
  margin: 0 auto 40px;
  padding-top: 10px;
  width: 36px;
}

.case-studies .item img.icon {
  width: 100px;
}

.case-studies .item p {
  text-align: left;
}

.intel-report {
  background-color: #FFF;
}

.intel-report strong {
  font-weight: 700;
}

.intel-report .caption {
  text-align: left;
}

.intel-report .title {
  font-size: 36px;
}

.contact {
  background-color: #eef3f4;
}

.contact strong {
  font-weight: 700;
}

.contact .title {
  font-size: 36px;
}

.contact .form-control {
  background-color: #FFF;
  width: 330px;
}

form {
  width: 330px;
  margin: 0px auto;
}

.features {
  padding-top: 0px;
}

.features .itemtext {
  background-color: #fff;
  padding: 30px;
}

.features .item {
  background-color: #fff;
  padding: 30px;
}

.features .itemtext h2 {
  font-weight: 700;
  margin-bottom: 22px;
  margin-top: 11px;
}

.features .itemtext img.icon {
  width: 100px;
}

.security {
  background-color: #eef3f4;
  color: #5a5a5a;
}

.security .solutions .item {
  background-color: #00649f;
  -webkit-box-shadow: 10px 10px 0px 0px #bed6e3;
  box-shadow: 10px 10px 0px 0px #bed6e3;
  color: #fff;
  margin-bottom: 22px;
  padding: 40px 10px;
}

.security .solutions .item h2 {
  font-weight: 700;
}

.security .solutions .item hr {
  border-top: 4px solid #fff;
  margin: 0 auto 40px;
  width: 36px;
}

.security .solutions .item img.icon {
  width: 100px;
}

.security .solutions .item .points {
  font-size: 14px;
}

.wrapper-agents-features {
  background: transparent url("https://img03.en25.com/EloquaImages/clients/NTTCommunications1/%7Bc7fecf74-623e-4040-a602-00772e16c80c%7D_ntt_banner2.jpg") center center no-repeat;
  background-size: cover;
}

.contact .item {
  vertical-align: top;
  display: inline-block;
  text-align: center;
  width: 330px;
}

.contact .caption {
  display: block;
}

/*remove padding for mobile devices*/
@media only screen and (max-width: 767px) {
  section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

@media only screen and (max-width: 480px) {
  .banners .item .blurb .lead {
    font-size: 46px;
  }
  .title {
    padding-top: 50px;
  }
}

/** **/
